.navbar {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  overflow: hidden;
  background-color: #E7E9EB;
}
.navbar .nav-icon {
  color: #c71d4d;
}

.navbar a {
  float: left;
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  color: black;
}

.navbar .icon {
  display: none;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 17px;
  border: none;
  outline: none;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.navbar a:hover,
.dropdown:hover .dropbtn {
  background-color: #555;
  color: white;
}

.dropdown-content a:hover {
  background-color: #ddd;
  color: black;
}

.dropdown:hover .dropdown-content {
  display: block;
}

@media screen and (max-width: 850px) {
  .navbar a:not(:first-child),
.dropdown .dropbtn {
    display: none;
  }

  .navbar a.icon {
    float: right;
    display: block;
  }
}
@media screen and (max-width: 850px) {
  .navbar.responsive {
    position: relative;
  }

  .navbar.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }

  .navbar.responsive a {
    float: none;
    display: block;
    text-align: left;
  }

  .navbar.responsive .dropdown {
    float: none;
  }

  .navbar.responsive .dropdown-content {
    position: relative;
  }

  .navbar.responsive .dropdown .dropbtn {
    display: block;
    width: 100%;
    text-align: left;
  }
}
.documents-header {
  font-size: 40px;
  padding: 20px;
  font-weight: 300;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.table-documents {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}
.table-documents .table {
  width: 100%;
  margin-top: 1rem;
  border-collapse: collapse;
  color: #666;
}
.table-documents .table-head tr th {
  text-overflow: ellipsis;
  font-weight: 600;
  font-size: 20px;
  text-align: left;
  padding-bottom: 1rem;
  border-bottom: 1px solid grey;
}
.table-documents .table tr {
  z-index: 0;
  border-bottom: 1px solid rgb(237, 235, 233);
}
.table-documents .table-body tr td {
  padding: 1rem 0.2rem;
}
.table-documents .table-body td:nth-child(1) {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font-size: 1.4rem;
}
.table-documents .table-body td:nth-child(1)::after {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  content: "";
  width: 2em;
  height: 2em;
  z-index: -2;
}

.file-icon.fa-folder-o {
  color: #666;
}
.file-icon.fa-file-excel-o {
  color: #49b35e;
}

@media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px) {
  table,
thead,
tbody,
th,
td,
tr {
    display: block;
    font-weight: 700;
  }

  thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  tr {
    border: 1px solid #ccc;
    border-collapse: collapse;
    margin: 20px;
    box-shadow: 0 0 0 0px #ccc;
  }

  #documents td {
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50%;
  }

  td:first-child {
    background-color: #E7E9EB;
  }

  td:before {
    position: absolute;
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    font-style: italic;
  }

  td:nth-of-type(1):before {
    content: "File Type";
  }

  td:nth-of-type(2):before {
    content: "Name";
  }

  td:nth-of-type(3):before {
    content: "Modified";
  }

  td:nth-of-type(4):before {
    content: "Modified By";
  }
}